import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListItemIcon from "@mui/material/ListItemIcon";

const dispatchedEvents = [];

const SrsMuiListItemIcon = function (props, ref) {
  return (
    <ListItemIcon {...props} theme={props.muiTheme}>
      <slot />
    </ListItemIcon>
  );
};

const styles = `
  /* Custom styles for the MuiListItemIcon component */
`;

const props = [
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ListItemIcon if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListItemIcon),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
